/* HexDataComponent.css */
.hex-data-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #282c34; /* Dark background color */
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.1); /* Light box shadow */
    border-radius: 8px;
  }
  
  .hex-data-section {
    margin-bottom: 20px;
  }
  
  .hex-data-title {
    font-size: 1.5em;
    font-weight: bold;
    margin-bottom: 10px;
    color: #61dafb; /* Light blue title color */
  }
  
  .hex-data-content {
    font-family: 'Arial', sans-serif;
    font-size: 1em;
    line-height: 1.6;
    color: #ffffff; /* White content text color */
    background-color: #363b44; /* Dark background color for content */
    padding: 10px;
    border-radius: 6px;
    overflow-x: auto; /* Allow horizontal scrolling if needed */
  }
  
  /* Add more styles as needed */
  